const utils = {
  // 
  clone (obj) {
    return obj ? JSON.parse(JSON.stringify(obj)) : null
  },

  /*  */
  isNumber (value) {
    return typeof value === "number" || value instanceof Number
  },

  /*  */
  isFunction (value) {
    return value && Object.prototype.toString.call(value) === "[object Function]"
  }
}

export default utils


